<script>
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import { API_BASE_URL } from '@/config';
import axios from 'axios';

import OrderItem from '@/components/OrderItem.vue';
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex'; // импотируем функцию из пакета vuex

export default {
  components: { BaseFormText, BaseFormTextarea, OrderItem },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',

      // orderAdded: false,
      orderAddSending: false,
    };
  },

  filters: { numberFormat },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice', amountProduct: 'cartAmountProduct' }), // выводим вычисляемое свойство высчитыв стоимость и общее кол-во в корзине
  },

  methods: {
    order() {
      // this.orderAdded = false;
      this.orderAddSending = true,

      this.formError = {};
      this.formErrorMessage = '';
      clearTimeout(this.loadProductsTimers); // таймер при вызове метода очищается, те при вызове нового старое сбрасывается
      this.loadProductsTimer = setTimeout(() => {
        axios
          .post(`${API_BASE_URL}/api/orders`, {
            ...this.formData,
          }, {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          })
          .then((response) => {
            this.$store.commit('resetCart');
            this.$store.commit('updateOrderInfo', response.data);

            this.$router.push({ name: 'orderInfo', params: { id: response.data.id } }); // указываем на какую страницу переместить и передаем параметр кот вернулся с формы
          })
          .catch((error) => {
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
          })

          .then(() => {
            // this.orderAdded = true;
            this.orderAddSending = false;
          });
      }, 3000);
    },
  },
};
</script>
