import Vue from 'vue';
import Vuex from 'vuex';
// не нужно из-за API import products from '@/data/products';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    // вариант для работы cartProducts: [ {productId: 1, amount: 2}],
    // amount - количество товара в корзине
    userAccessKey: null, // для хранения ключа пользователя
    cartProductsData: [], // для корзины items
    cartLoading: false,
    orderInfo: null,
  },
  mutations: { // через мутации меняем св-во состояния и операции дб синхронными, а в иных ситуациях можно использовать действия actions
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) { // работа с корзиной оформление заказа
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    /* не нужна после изменений с API
   addProductToCart(state, {productId, amount}) {  // state - обьект состояния хранилища, ayload инф кот-я передается
      const item = state.cartProducts.find(item => item.productId === productId);

      if(item){
        item.amount += amount;

      }else{
        state.cartProducts.push({
        productId: productId, // из ProductPage передаем, а здесь получаем productId
        amount: amount,
      });
      }
    }, */
    updateCartProductAmount(state, { productId, amount }) { // принимаем два аргумента {индификатор, количество} которые хотим менять в корзине
      const item = state.cartProducts.find((item) => item.productId === productId); // находим товар в корзине, существует ли
      if (item) {
        item.amount = amount;
      }
    },
    /* deleteCartProduct(state, productId){
      state.cartProducts = state.cartProducts.filter(item => item.productId !== productId);
    }, */
    updateUserAccessKey(state, accessKey) { // через мутацию и actions меняем св-во updateUserAccessKey
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) { // преобразование данных
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        amount: item.quantity,
      }));
    },

  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map((item) => {
        const { product } = state.cartProductsData.find((p) => p.product.id === item.productId);

        return {
          ...item,
          product: {
            ...product,
            image: product.image.file.url,
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
    },
    cartAmountProduct(state, getters) {
      return getters.cartDetailProducts.reduce((acc, item) => (item.product.id === item.productId) + acc, 0);
    },
  },
  actions: {
    loadOrderInfo(context, orderId) { // страница оформления заказа
      return axios
        .get(`${API_BASE_URL}/api/orders/${orderId}`, {
          params: {
            userAccessKey: context.state.userAccessKey, // передаем ключ
          },
        })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        });
    },

    loadCart(context) {
      this.state.cartLoading = true;
      return (new Promise((resolve) => setTimeout(resolve, 2000)))
        .then(() => {
          axios
            .get(`${API_BASE_URL}/api/baskets`, {
              params: {
                userAccessKey: context.state.userAccessKey, // передаем ключ
              },
            })
            .then((response) => {
              if (!context.state.userAccessKey) {
                localStorage.setItem('userAccessKey', response.data.user.accessKey); // сохраняем ключ, чтобы при обновлении стр сохранялась корзина
                // вызываем мутации для передавая нужную информацию передаем ключик пользователя с API accesskey
                context.commit('updateUserAccessKey', response.data.user.accessKey);
              }
              // и в items храняться св-ва о товаре
              context.commit('updateCartProductsData', response.data.items);
              // а вызываем в главном компаненте App
              context.commit('syncCartProducts'); // синхронизация товара из API
              this.state.cartLoading = false;
            });
        });
    },
    addProductToCart(context, { productId, amount }) {
      return (new Promise((resolve) => setTimeout(resolve, 2000)))
        .then(() => axios
          .post(`${API_BASE_URL}/api/baskets/products`, {
            productId,
            quantity: amount,
          }, {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          })
          .then((response) => {
            context.commit('updateCartProductsData', response.data.items);
            // а вызываем в главном компаненте App
            context.commit('syncCartProducts'); // синхронизация товара из API
          }));
    },
    updateCartProductAmount(context, { productId, amount }) {
      context.commit('updateCartProductAmount', { productId, amount }); // вызываем мутацию и передаем данные

      if (amount < 1) {
        return;
      }
      return axios
        .put(`${API_BASE_URL}/api/baskets/products`, {
          productId,
          quantity: amount,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items); // обновление товара
        })
        .catch(() => {
          context.commit('syncCartProducts'); // синхронизация товара из API при ошибке
        });
    },
    deleteCartProduct(context, productId) {
      // context.state.cartProducts = context.state.cartProducts.filter(item => item.productId !== productId);
      return axios
        .delete(`${API_BASE_URL}/api/baskets/products`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
          data: {
            productId,
          },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items); // обновление товара
          context.commit('syncCartProducts');
        })
        .catch(() => {
          context.commit('syncCartProducts'); // синхронизация товара из API при ошибке
        });
    },

  },
});
