<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <button class="pagination__link pagination__link--arrow" href="#" aria-label="Предыдущая страница"
        @click.prevent="prevPage">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </button>
    </li>
    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
      <a href="#" class="pagination__link" :class="{ 'pagination__link--current': pageNumber === page }"
        @click.prevent="paginate(pageNumber)">
        {{ pageNumber }}
      </a>
    </li>

    <li class="pagination__item">
      <button class="pagination__link pagination__link--arrow" href="#" aria-label="Следующая страница"
        @click.prevent="nextPage">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </button>
    </li>
  </ul>
</template>

<script>
// import { computed } from 'vue';

export default {
  model: {
    prop: 'page', // св-во которое передается будет записыв в page
    event: 'paginate', // событие кот-е должны передать
  },
  props: ['page', 'count', 'perPage'], // входящие параметры, которые примет компанент: значение стр, общее кол-во элементов и кол-во выводимое на одной стр
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage); // высчитываем кол-во страниц
    },
    /* pageNext() {
      return this.pageNumber++;
    },
    pagePrev() {
      return this.pageNumber--;
    }, */
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page); // передаем событие v-model, номер стр на кот-ю переключ-ся
    },
    nextPage() {
      if (this.page >= 1 && Math.ceil(this.count / this.perPage) > this.page) {
        this.$emit('paginate', this.page + 1);
      }
    },
    prevPage() {
      if (this.page > 1) {
        this.$emit('paginate', this.page - 1);
      }
    },
  },
};
</script>
