<template>

<li class="cart__order">
              <h3>{{ item.product.title }}</h3>
              <b> {{ item.product.price }} ₽</b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
      <!--  <li class="cart__order">
              <h3>Гироскутер Razor Hovertrax 2.0ii</h3>
              <b>4 990 ₽</b>
              <span>Артикул: 150030</span>
            </li>-->

</template>

<script>
import numberFormat from '@/helpers/numberFormat';
// import {mapMutations} from 'vuex';
import { mapActions } from 'vuex';

export default {
  filters: { numberFormat },
  props: ['item'],
  computed: {
    amount: {
      get() {
        return this.item.amount; // возвращает параметр
      },
      set(value) { // вызывает метод мутации ф-ция прописана в CartPage
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value });
        //  productId получаем из входного параметра и аmount - количество получаем из того значения кот-е приходит в вычисляемое св-во метод set
      },
    },
  },
};

</script>
