<template>
 <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :color-id.sync="filterColorId"/>

      <section class="catalog">

        <div v-if="productsLoading">Загрузка товаров... они уже бегут к вам на страницу...</div>
        <img v-if="productsLoading" src="../../public/img/Glowing ring.gif" alt="гифка" style="width: 150px; height: 150px; margin-top: 100px; margin-left: 250px;">

        <div v-if="productsLoadingFailed">Произошла ошибка при загрузке товаров...
        <button @click.prevent="loadProducts">Попробовать еще раз</button>
        </div>
        <ProductList :products="products"/>
       <!--  Вариант длинного пути <ProductList :products="products" @gotoPage="(pageName, pageParams) => $emit('gotoPage', pageName, pageParams)"/>-->
        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage"/>
      </section>

    </div>
  </main>
</template>

<script>

// после подключения API не нужно import products from '@/data/products'; относительный путь ./. заменяем на абсолютный к src через @ и он будет работать везде
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '../config';

export default {
  components: { ProductList, BasePagination, ProductFilter }, // компонента только для этого файла, в ином случае надо импортировать
  // ProductList: ProductList м записать одним словом ProductList
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,
      page: 1,
      productsPerPage: 3, // количество товара на странице
      productsData: null, // хранится ответ с API
      productsLoading: false, // статус загрузки false -не идет загрузка
      productsLoadingFailed: false, // статус произошла ли ошибка пизагрузке
    };
  },
  computed: {
    /* после подключения API не нужно
   filteredProducts() {
      let filteredProducts = products;

      if (this.filterPriceFrom > 0) {
        filteredProducts = filteredProducts.filter(product => product.price > this.filterPriceFrom);
      }
      if (this.filterPriceTo > 0) {
        filteredProducts = filteredProducts.filter(product => product.price < this.filterPriceTo);
      }
      if (this.filterCategoryId) {
        filteredProducts = filteredProducts.filter(product => product.categoryId === this.filterCategoryId);
      }
      if (this.filterColorId) {

        filteredProducts = filteredProducts.filter(product => product.colorId === this.filterColorId);
      }
      return filteredProducts;
    }, */
    products() { // вычисляемеое свойство формирующее типо массива
      return this.productsData
        ? this.productsData.items.map((product) => ({
          ...product,
          image: product.image.file.url, // меняем формат картинки
        }))
        : [];
      // productsData хранится обьект, items хранится массивб возвращаем товар или пустой массив
      // const offset = (this.page - 1) * this.productsPerPage;  вырезает определен товары в начальный индекс
      // return this.filteredProducts.slice(offset, offset + this.productsPerPage);  расчет конечного индекса
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0; // return this.filteredProducts.length; возвращает общее кол-во товара по фильтру
    },
  },
  methods: {
    loadProducts() { // метод загружающий данные
      this.productsLoading = true; // в начале статус загрузки сбрасываем, те он идет
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimers); // таймер при вызове метода очищается, те при вызове нового старое сбрасывается
      this.loadProductsTimer = setTimeout(() => { // setTimeout прописан до 2 сек, чтоб метод вызывался без задержки и записали через this как св-во экземпляра
        axios
          .get(`${API_BASE_URL}/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              colorId: this.filterColorId,
            },
          })
          .then((response) => this.productsData = response.data) // после получения данных статус загрузки меняется
          .catch(() => this.productsLoadingFailed = true) // catch ловит ошибку которая происходит
          .then(() => this.productsLoading = false);
      }, 2000);
    },
  },
  watch: {
    // наблюдатель, если страница изменилась то вызывается метод, стр перезагружается, товары меняются на каждой страницы
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
  },
  created() { // вызывается метод когда компонент создается
    this.loadProducts();
  },
};
</script>
